.contacts {
  &__wrapper {
    padding: 25px 15px;
  }

  &__item {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, auto);
    row-gap: 21px;
  }

  &__btn {
    margin: 30px auto;
  }

  &__toggles {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 15px 0 10px 0;
  }

  &__toggle {
    font-family: "Cormorant Garamond", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #000000;
    border: none;
    outline: none;
    background-color: transparent;
    cursor: pointer;
    box-sizing: border-box;

    &--current {
      border-bottom: 2px solid @dark-grayish;
    }
  }

  &__map {
    width: 100%;
  }
}
