.portfolio {
  &__wrapper {
    padding: 25px 15px;
  }

  &__slider {
    padding: 15px 0;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(137px, 1fr));
    grid-template-rows: repeat(2, 184);
    gap: 15px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__text,
  &__link {
    font-family: "Cormorant Garamond", sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 19px;
    text-align: center;
    color: inherit;
  }

  &__link {
    border-bottom: 1px solid @black;
  }

  &__img {
    width: 100%;
  }
}
