.footer {
  padding: 25px 0;
  &__wrapper {
    padding: 0 15px;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(5, auto);
    row-gap: 15px;
  }

  &__logo {
    justify-self: center;
  }

  &__socials {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }

  &__promotions {
    font-family: "Cormorant Garamond", sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: 22px;
  }
}

.footer-nav {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 15px;
  &__link {
    font-family: Raleway, sans-serif;
    font-size: 13px;
    font-weight: 300;
    line-height: 17px;
    color: @black;
  }
}
