.promo {
  padding: 17px 0 10px 0;
  &__wrapper {
    padding: 0 15px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(4, auto);
    grid-column-gap: 15px;
  }

  &__header {
    grid-column-start: 1;
    grid-column-end: -1;
    grid-row-start: 1;
    grid-row-end: 2;
  }

  &__title {
    font-family: "Cormorant Garamond", sans-serif;
    font-size: 25px;
    font-weight: 400;
    line-height: 34px;
    font-style: italic;
    text-align: left;

    &--up {
      font-style: normal;
      text-transform: uppercase;
    }

    @media (min-width: 347px) {
      font-size: 28px;
    }
  }

  &__picture {
    grid-column-start: 2;
    grid-column-end: -1;
    grid-row-start: 1;
    grid-row-end: 3;
    padding: 66px 0 30px 0;
    justify-self: end;
  }

  &__list {
    grid-column-start: 1;
    grid-column-end: -1;
    grid-row-start: 2;
    display: grid;
    grid-template-columns: repeat(auto-fill, 45px);
    column-gap: 4.5px;
  }

  &__img {
    display: inline-block;
    &--cropping-top-left {
      border-radius: 30px 0 0 0;
    }

    &--cropping-top-right {
      border-radius: 0 30px 0 0;
    }
  }

  &__btn,
  &__link {
    grid-column-start: 1;
    grid-column-end: -1;
    justify-self: center;
  }

  &__link {
    padding: 15px 0;
  }
}
