.gift {
  &__wrapper {
    padding: 25px 15px;
    display: flex;
    flex-direction: column;
  }

  &__img {
    margin: 0 auto;
  }

  &__title {
    padding: 19px 0 15px 0;
  }

  &__descr {
    text-align: center;
    padding-bottom: 15px;
  }

  &__link {
    padding-top: 30px;
  }

  &__btn {
    align-self: center;
  }
}
