.button {
  padding: 10px 20px ;
  display: inline-block;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
  text-align: center;
  color: #000000;
  text-transform: uppercase;
  box-sizing: border-box;
  border: 1px solid #000000;
  cursor: pointer;
}
.menu-btn {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 23px;
  height: 16px;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  /* border: 3px solid #fff; */
}
.menu-btn__burger {
  width: 23px;
  height: 2px;
  background: #000000;
  border-radius: 5px;
  transition: all 0.5s ease-in-out;
}
.menu-btn__burger::before,
.menu-btn__burger::after {
  content: '';
  left: 0;
  position: absolute;
  width: 23px;
  height: 2px;
  background: #000000;
  border-radius: 5px;
  transition: all 0.5s ease-in-out;
}
.menu-btn__burger::before {
  transform: translateY(-7px);
}
.menu-btn__burger::after {
  transform: translateY(7px);
}
/* ANIMATION */
.menu-btn.open .menu-btn__burger {
  transform: translateX(-50px);
  background: transparent;
  box-shadow: none;
}
.menu-btn.open .menu-btn__burger::before {
  transform: rotate(45deg) translate(35px, -35px);
}
.menu-btn.open .menu-btn__burger::after {
  transform: rotate(-45deg) translate(35px, 35px);
}
/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}
.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}
.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}
.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir="rtl"] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
  display: none;
}
.page {
  height: 100%;
  font-size: 13px;
  font-family: "Raleway", sans-serif;
  font-weight: 300;
  line-height: 17px;
  text-align: center;
}
.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-width: 320px;
  min-height: 100%;
  max-width: 1440px;
  margin: 0 auto;
  background-color: #fffaf3;
  color: #000000;
}
.page-header {
  padding: 20px 15px 0 15px;
}
.phone-link {
  font-weight: 400;
  font-size: 13px;
  text-align: center;
  color: #181818;
}
.main-nav {
  position: relative;
  width: auto;
  min-height: 27px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-row-gap: 30px;
}
.main-nav__logo {
  grid-column-start: 2;
  grid-row-start: 1;
  justify-self: end;
  align-self: center;
}
.main-nav__burger {
  grid-column-start: 1;
  grid-column-end: 2;
}
.main-nav__container {
  grid-column: span 2;
  display: grid;
  grid-template-columns: 1fr;
}
.main-nav__list {
  padding: 30px 0;
  display: grid;
  row-gap: 20px;
}
.main-nav__link {
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  color: #000000;
}
.main-nav__btn {
  justify-self: center;
}
.main-nav--opened {
  padding-bottom: 30px;
}
.main-nav--closed .main-nav__list {
  padding: 0;
}
@media (max-width: 1024px) {
  .main-nav--closed .main-nav__item,
  .main-nav--closed .main-nav__phone,
  .main-nav--closed .main-nav__btn {
    display: none;
  }
}
.page-main__title {
  padding: 0 15px 0 15px;
  font-family: Raleway;
  font-size: 13px;
  font-weight: 300;
  line-height: 17px;
  text-align: left;
}
.link {
  font-family: "Raleway", sans-serif;
  font-size: 13px;
  font-weight: 300;
  line-height: 15px;
  text-align: center;
  text-decoration: underline;
  cursor: pointer;
  color: #000000;
}
.title {
  font-family: "Cormorant Garamond", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 29px;
  text-align: center;
}
.service {
  padding: 0 0 25px 0;
}
.service__wrapper {
  padding: 0 15px;
  margin: 0 auto;
}
.service__toggles {
  display: grid;
  grid-template-columns: repeat(4, min-content);
  column-gap: 12px;
}
.service__toggle {
  font-family: "Cormorant Garamond", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
  border: none;
  outline: none;
  background-color: transparent;
  padding: 9px 0 4px 0;
  cursor: pointer;
  box-sizing: border-box;
}
.service__toggle--current {
  border-bottom: 2px solid #9da57c;
}
.contacts__wrapper {
  padding: 25px 15px;
}
.contacts__item {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(4, auto);
  row-gap: 21px;
}
.contacts__btn {
  margin: 30px auto;
}
.contacts__toggles {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 15px 0 10px 0;
}
.contacts__toggle {
  font-family: "Cormorant Garamond", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
  border: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
  box-sizing: border-box;
}
.contacts__toggle--current {
  border-bottom: 2px solid #9da57c;
}
.contacts__map {
  width: 100%;
}
.footer {
  padding: 25px 0;
}
.footer__wrapper {
  padding: 0 15px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(5, auto);
  row-gap: 15px;
}
.footer__logo {
  justify-self: center;
}
.footer__socials {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.footer__promotions {
  font-family: "Cormorant Garamond", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
}
.footer-nav {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 15px;
}
.footer-nav__link {
  font-family: Raleway, sans-serif;
  font-size: 13px;
  font-weight: 300;
  line-height: 17px;
  color: #000000;
}
.checkbox {
  display: flex;
}
.checkbox__input {
  display: none;
}
.checkbox__label {
  display: flex;
  flex-grow: 1;
  align-items: center;
  position: relative;
  font-size: 18px;
  font-weight: 500;
  line-height: 18px;
  cursor: pointer;
}
.checkbox__label::before {
  align-self: start;
  content: "";
  top: 0;
  left: 0;
  flex: 0 0 24px;
  height: 24px;
  margin-right: 10px;
  border: 1px solid #000000;
  background-color: inherit;
}
.checkbox__label::after {
  content: "";
  width: 19px;
  height: 19px;
  position: absolute;
  top: 7px;
  left: 8px;
}
.checkbox__label span {
  font-family: Raleway, sans-serif;
  font-size: 12px;
  font-weight: 300;
  line-height: 18px;
  text-align: left;
}
.checkbox__link {
  text-decoration: underline;
  color: #000000;
}
.checkbox__input:checked + .checkbox__label::after {
  background-image: url("../../img/svg/tick.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.statistics {
  padding: 25px 15px;
}
.statistics__list {
  margin: 0;
  display: grid;
  row-gap: 15px;
}
.statistics__item {
  display: grid;
  row-gap: 15px;
}
.statistics__indicator {
  font-family: "Cormorant Garamond", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 29px;
}
.statistics__descr {
  margin: 0;
}
.promotions {
  padding: 25px 0;
}
.promotions__item {
  display: grid;
  row-gap: 20px;
}
.promotions__content {
  padding: 20px 15px 5px 15px;
  display: grid;
  row-gap: 15px;
}
.promotions__img {
  width: 100%;
}
.promo {
  padding: 17px 0 10px 0;
}
.promo__wrapper {
  padding: 0 15px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(4, auto);
  grid-column-gap: 15px;
}
.promo__header {
  grid-column-start: 1;
  grid-column-end: -1;
  grid-row-start: 1;
  grid-row-end: 2;
}
.promo__title {
  font-family: "Cormorant Garamond", sans-serif;
  font-size: 25px;
  font-weight: 400;
  line-height: 34px;
  font-style: italic;
  text-align: left;
}
.promo__title--up {
  font-style: normal;
  text-transform: uppercase;
}
@media (min-width: 347px) {
  .promo__title {
    font-size: 28px;
  }
}
.promo__picture {
  grid-column-start: 2;
  grid-column-end: -1;
  grid-row-start: 1;
  grid-row-end: 3;
  padding: 66px 0 30px 0;
  justify-self: end;
}
.promo__list {
  grid-column-start: 1;
  grid-column-end: -1;
  grid-row-start: 2;
  display: grid;
  grid-template-columns: repeat(auto-fill, 45px);
  column-gap: 4.5px;
}
.promo__img {
  display: inline-block;
}
.promo__img--cropping-top-left {
  border-radius: 30px 0 0 0;
}
.promo__img--cropping-top-right {
  border-radius: 0 30px 0 0;
}
.promo__btn,
.promo__link {
  grid-column-start: 1;
  grid-column-end: -1;
  justify-self: center;
}
.promo__link {
  padding: 15px 0;
}
.manicure {
  padding: 10px 0 30px 0;
  display: grid;
  row-gap: 10px;
}
.manicure__item {
  display: flex;
  justify-content: space-between;
}
.manicure__descr {
  text-align: left;
}
.manicure__price {
  text-align: right;
  white-space: nowrap;
}
.socials__icon {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
}
.socials__item:not(:last-child) {
  margin-right: 13px;
}
.portfolio__wrapper {
  padding: 25px 15px;
}
.portfolio__slider {
  padding: 15px 0;
}
.portfolio__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(137px, 1fr));
  grid-template-rows: repeat(2, 184);
  gap: 15px;
}
.portfolio__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.portfolio__text,
.portfolio__link {
  font-family: "Cormorant Garamond", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
  text-align: center;
  color: inherit;
}
.portfolio__link {
  border-bottom: 1px solid #000000;
}
.portfolio__img {
  width: 100%;
}
.gift__wrapper {
  padding: 25px 15px;
  display: flex;
  flex-direction: column;
}
.gift__img {
  margin: 0 auto;
}
.gift__title {
  padding: 19px 0 15px 0;
}
.gift__descr {
  text-align: center;
  padding-bottom: 15px;
}
.gift__link {
  padding-top: 30px;
}
.gift__btn {
  align-self: center;
}
.reviews__wrapper {
  padding: 0 15px;
}
.reviews__header {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 103px min-content;
}
.reviews__title {
  grid-column-start: 1;
  grid-column-end: -1;
  grid-row-start: 1;
  grid-row-end: 2;
  z-index: 1;
  font-family: "Cormorant Garamond", sans-serif;
  font-size: 24px;
  font-style: italic;
  font-weight: 300;
  line-height: 29px;
  text-align: left;
}
.reviews__title--style {
  font-size: 24px;
  font-weight: 400;
  line-height: 29px;
  font-style: normal;
}
.reviews__title--align {
  padding-left: 30px;
}
.reviews__img {
  grid-column-start: 2;
  grid-row-start: 1;
  grid-row-end: -1;
  justify-self: end;
}
.reviews__list {
  position: relative;
}
.reviews__icon {
  position: absolute;
  top: 0;
  left: 0;
}
.reviews__item {
  margin: 0;
  padding: 0 25px;
  display: grid;
  grid-template-areas: "author author" "star date" "text text";
  row-gap: 7px;
}
.reviews__author-name {
  grid-area: author;
  justify-self: left;
}
.reviews__star {
  grid-area: star;
  display: grid;
  grid-template-columns: repeat(5, 13px);
  column-gap: 3px;
}
.reviews__date {
  grid-area: date;
  justify-self: left;
}
.reviews__text {
  grid-area: text;
  text-align: left;
}
.form {
  display: grid;
  row-gap: 20px;
}
.form__wrapper {
  display: grid;
  grid-template-columns: 1fr 70px;
}
.form__label {
  border: 1px solid #000000;
  border-right: none;
}
.form__input {
  display: flex;
  padding: 12px 15px;
  background-color: #fffaf3;
  border: none;
  outline: none;
}
.form__btn {
  width: 70px;
  height: 42px;
  position: relative;
  padding: 0;
  background-color: inherit;
  border: none;
  border-top: 1px solid #000000;
  border-right: 1px solid #000000;
  border-bottom: 1px solid #000000;
  cursor: pointer;
}
.form__btn::after {
  content: "";
  position: absolute;
  width: 49px;
  height: 8px;
  top: 40%;
  left: 0;
  background-image: url("../../img/svg/arrow.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
.burger {
  position: absolute;
  top: 8px;
  left: -12px;
  width: 100%;
}
.burger__icon {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  fill: none;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
  cursor: pointer;
}
@media (min-width: 1024px) {
  .burger {
    display: none;
  }
}
.box {
  width: 19.3333%;
  height: 100%;
  float: left;
}
#b1 {
  background: inherit;
}
.burger__icon {
  stroke: #000000;
}
.carousel__list {
  width: 100vw;
}
.carousel__list .slick-dots {
  display: flex !important;
  flex-direction: row;
  justify-content: center;
}
.carousel__list .slick-dots button {
  font-size: 0;
  padding: 0;
  width: 9px;
  height: 9px;
  border-radius: 50%;
  border: none;
