  background-color: #9da57c;
}
.carousel__list .slick-dots .slick-active button {
  background-color: #fffaf3;
  border: 1px solid #000000;
}
.carousel__list .slick-dots li:not(:last-child) {
  margin-right: 6px;
  align-content: center;
}
