.burger {
  position: absolute;
  top: 8px;
  left: -12px;
  width: 100%;

  &__icon {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 100%;
    fill: none;
    stroke-width: 2;
    stroke-linecap: round;
    stroke-linejoin: round;
    cursor: pointer;
  }

  @media (min-width: 1024px) {
    display: none;
  }
}

.box {
  width: 19.3333%;
  height: 100%;
  float: left;
}

#b1 { background: inherit; }
.burger__icon { stroke: #000000;}


