.main-nav {
  position: relative;
  width: auto;
  min-height: 27px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-row-gap: 30px;

  &__logo {
    grid-column-start: 2;
    grid-row-start: 1;
    justify-self: end;
    align-self: center;
  }

  &__burger {
    grid-column-start: 1;
    grid-column-end: 2;
  }

  &__container {
    grid-column: span 2;
    display: grid;
    grid-template-columns: 1fr;
  }

  &__list {
    padding: 30px 0;
    display: grid;
    row-gap: 20px;
  }

  &__link {
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: @black;
  }

  &__btn {
    justify-self: center;
  }
}

.main-nav--opened {
  padding-bottom: 30px;
}

.main-nav--closed .main-nav__list {
  padding: 0;
}


@media (max-width: 1024px) {
  .main-nav--closed .main-nav__item,
  .main-nav--closed .main-nav__phone,
  .main-nav--closed .main-nav__btn {
    display: none;
  }
}
