// Raleway

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src:
    url("../../fonts/Raleway-Light.woff") format("woff"),
    url("../../fonts/Raleway-Light.woff2") format("woff2");
}

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src:
    url("../../fonts/Raleway-Regular.woff") format("woff"),
    url("../../fonts/Raleway-Regular.woff2") format("woff2");
}

