.reviews {
  &__wrapper {
    padding: 0 15px;
  }

  &__header {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 103px min-content;
  }

  &__title {
    grid-column-start: 1;
    grid-column-end: -1;
    grid-row-start: 1;
    grid-row-end: 2;
    z-index: 1;
    font-family: "Cormorant Garamond", sans-serif;
    font-size: 24px;
    font-style: italic;
    font-weight: 300;
    line-height: 29px;
    text-align: left;

    &--style {
      font-size: 24px;
      font-weight: 400;
      line-height: 29px;
      font-style: normal;
    }

    &--align {
      padding-left: 30px;
    }
  }

  &__img {
    grid-column-start: 2;
    grid-row-start: 1;
    grid-row-end: -1;
    justify-self: end;
  }

  &__list {
    position: relative;
  }

  &__icon {
    position: absolute;
    top: 0;
    left: 0;
  }

  &__item {
    margin: 0;
    padding: 0 25px;
    display: grid;
    grid-template-areas:
    "author author"
    "star date"
    "text text";
    row-gap: 7px;
  }

  &__author-name {
    grid-area: author;
    justify-self: left;
  }

  &__star {
    grid-area: star;
    display: grid;
    grid-template-columns: repeat(5, 13px);
    column-gap: 3px;

  }

  &__date {
    grid-area: date;
    justify-self: left;
  }

  &__text {
    grid-area: text;
    text-align: left;
  }
}
