.checkbox {
  display: flex;

  &__input {
    display: none;
  }

  &__label {
    display: flex;
    flex-grow: 1;
    align-items: center;
    position: relative;
    font-size: 18px;
    font-weight: 500;
    line-height: 18px;
    cursor: pointer;

    &::before {
      align-self: start;
      content: "";
      top: 0;
      left: 0;
      flex: 0 0 24px;
      height: 24px;
      margin-right: 10px;
      border: 1px solid @black;
      background-color: inherit;
      }

    &::after {
      content: "";
      width: 19px;
      height: 19px;
      position: absolute;
      top: 7px;
      left: 8px;
    }
  }

  &__label span {
    font-family: Raleway, sans-serif;
    font-size: 12px;
    font-weight: 300;
    line-height: 18px;
    text-align: left;
  }

  &__link {
    text-decoration: underline;
    color: @black;
  }
}

.checkbox__input:checked + .checkbox__label::after {
  background-image: url("../../img/svg/tick.svg");
  background-repeat: no-repeat;
  background-position: center;
}
