.carousel {
  &__list {
    width: 100vw;

    .slick-dots {
      display: flex !important;
      flex-direction: row;
      justify-content: center;

      button {
        font-size: 0;
        padding: 0;
        width: 9px;
        height: 9px;
        border-radius: 50%;
        border: none;
        box-sizing: border-box;
        background-color: @dark-grayish;
      }

      .slick-active button {
        background-color: @pale-orange;
        border: 1px solid @black;
      }
    }

    .slick-dots li:not(:last-child) {
      margin-right: 6px;
      align-content: center;
    }
  }
}



