.service {
  padding: 0 0 25px 0;

  &__wrapper {
    padding: 0 15px;
    margin: 0 auto;
  }

  &__toggles {
    display: grid;
    grid-template-columns: repeat(4, min-content);
    column-gap: 12px;
  }

  &__toggle {
    font-family: "Cormorant Garamond", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #000000;
    border: none;
    outline: none;
    background-color: transparent;
    padding: 9px 0 4px 0;
    cursor: pointer;
    box-sizing: border-box;

    &--current {
      border-bottom: 2px solid @dark-grayish;
    }
  }
}
