.statistics {
  padding: 25px 15px;

  &__list {
    margin: 0;
    display: grid;
    row-gap: 15px;
  }

  &__item {
    display: grid;
    row-gap: 15px;
  }

  &__indicator {
    font-family: "Cormorant Garamond", sans-serif;
    font-size: 24px;
    font-weight: 400;
    line-height: 29px;
  }

  &__descr {
    margin: 0;
  }
}
